*{
  box-sizing: border-box;
}

$color: steelblue;

.my-flix {
  color: $color
}



h1 {
 color: lightgray;
 font-family: 'Lobster', cursive;
 text-align: center;
 font-size: 3em;
}

h2 {
  color: lightgray;
  font-family: 'Lato', sans-serif;
  text-align: center;
}

p {
  color: white;
  font-family: 'Lato', sans-serif;
}

.columns {
  column-count: 2;
  column-gap: 40px;
}

html {
  background-color: grey;
  background-image: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1740&q=80");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.movie-poster {
  text-align: center;
}

.movie-view {
  color: white;
}

.label {
  color: white;
}

.font {
  background-color: rgb(99, 99, 99);
  align-items: center;
}

.movie-card{
  color: white;
}

label.form-label {
  color: white;
}

body {
  text-align: center;
}


.container {
text-align: center;
display: inline-block;
margin-right: auto;
margin-left: auto;
bottom:5px;
width: 200px;
z-index: 10;
padding: 50px 20px;
}

button {
  margin: 15px 0;
  padding: 6%;
  border-radius: 10px;
  border: none;
}


.movie-title {
  color: white;
  background-color: rgb(59, 4, 4);
}

.movie-description {
  background-color: rgb(88, 85, 85);
  color: white
}

